.story-detail {
  display: flex;
  flex-direction: row;
  height: calc(100vh - 48px);
}
.detail {
  display: flex;
  flex-direction: column;
}
.actions {
  display: flex;
  justify-content: space-between;
  margin-bottom: 16px;
}
.mark,
.font-actions button {
  padding: 4px 12px;
  border-radius: 4px;
  border: 1px solid #dadafa;
  outline: none;
  font-size: 17px;
  color: #6b6ba1;
  background: #e2e2ed8a;
}
.mark:hover,
.font-actions button:hover {
  background: #6b6ba1;
  color: #fff;
}
.back-btn {
  font-size: 24px;
  padding: 0;
}
.image {
  width: 300px;
  height: auto;
  border-radius: 4px;
}
.font {
  display: flex;
  justify-content: space-between;
}
.default {
  margin: 0 4px;
}
.font-size {
  font-weight: 800;
  color: #6b6ba1;
  font-size: 18px;
  padding: 0 8px;
}
.name {
  margin: 8px 0 18px;
  font-size: 28px;
}
.author {
  padding: 0 8px;
  font-size: 17px;
  color: rgb(43, 29, 56);
}
.script {
  overflow-y: scroll;
  font-size: 16px;
  line-height: 28px;
  padding: 0 24px;
  margin-right: -30px;
}
.audio {
  margin: 16px 0;
}
audio::-webkit-media-controls-panel {
  background: #e2e2ed8a;
  width: 300px;
}
button {
  cursor: pointer;
}
.script ::selection {
  background: #e2e2ed8a;
  color: #b006d6;
}
@media screen and (max-width: 880px) {
  .story-detail {
    flex-direction: column;
    align-items: center;
    height: unset;
  }
  .script {
    margin-top: 36px;
    overflow-y: unset;
    margin-right: 0;
  }
  .detail {
    width: 100%;
    align-items: center;
  }
  .actions {
    width: 100%;
  }
}
